.main-product {}

.main-product__image {}

.main-product__title {
  font-size: 16px;
  line-height: 20px;
  color: #222222;
  text-transform: uppercase;
}

.main-product__description {}

.main-product__list {
  width: 142px;
  padding: 0;
  margin: 0;

  font-family: arial;
  font-size: 12px;
